.alert {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  bottom: 0;
  left: 0;
  background-color: #17171744;
  width: 100%;
  height: 100%;
}
.alert__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  background-color: #041613;
  width: 70%;
  height: 25%;
  border: 2px solid #07b3a4;
}
.alert__message {
  color: whitesmoke;
  text-align: center;
}
.alert__button {
  width: 35%;
  height: 2rem;
  margin: 3rem 0 0 0;
  background-color: #07b3a4;
  color: #e2e2e2;
  border: none;
  cursor: pointer;
}
.alert__button:hover {
  color: #bdbdbd;
  background-color: #10534e;
}